import React, { useState, useEffect } from 'react';
import { MusicPlayerProps } from '../types';
import { playMidiReal, stopMidi, onPlayStateChange } from '../utils/midiPlayer';

const MusicPlayer: React.FC<MusicPlayerProps> = ({ music, midiData }) => {
  const [isPlayingState, setIsPlayingState] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(30); // 30

  // 
  useEffect(() => {
    const unsubscribe = onPlayStateChange(setIsPlayingState);
    return unsubscribe;
  }, []);

  // 
  useEffect(() => {
    let intervalId: NodeJS.Timeout | null = null;
    
    if (isPlayingState && midiData) {
      intervalId = setInterval(() => {
        setCurrentTime(prev => {
          if (prev >= duration) {
            if (intervalId) clearInterval(intervalId);
            return 0;
          }
          return prev + 0.1;
        });
      }, 100);
    } else if (!isPlayingState && intervalId) {
      clearInterval(intervalId);
    }
    
    return () => {
      if (intervalId) clearInterval(intervalId);
    };
  }, [isPlayingState, midiData, duration]);

  const handlePlayPause = async () => {
    if (!midiData) {
      console.warn('MIDI');
      return;
    }

    if (isPlayingState) {
      // 
      stopMidi();
    } else {
      // 
      try {
        // music.style
        await playMidiReal(midiData, music?.style || 'classical');
      } catch (error) {
        console.error(':', error);
      }
    }
  };

  const handleStop = () => {
    stopMidi();
    setCurrentTime(0);
  };

  const handleSeek = (e: React.ChangeEvent<HTMLInputElement>) => {
    const time = parseFloat(e.target.value);
    setCurrentTime(time);
  };

  if (!music) {
    return (
      <div className="p-8 text-center text-gray-500">
        
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="mb-6">
        <h3 className="text-xl font-semibold text-white">{music.style} </h3>
        <p className="text-gray-300 mt-2">{music.description}</p>
        <div className="flex items-center mt-3 text-sm text-gray-400">
          <span>: </span>
          <span className={`ml-2 px-2 py-1 rounded ${music.status === 'generated' ? 'bg-green-500/20 text-green-400' : 'bg-yellow-500/20 text-yellow-400'}`}>
            {music.status === 'generated' ? '' : ''}
          </span>
        </div>
      </div>

      {/*  */}
      <div className="bg-gray-900/50 rounded-xl p-6">
        <div className="flex items-center justify-center gap-4 mb-6">
          <button
            onClick={handlePlayPause}
            disabled={!midiData}
            className={`w-16 h-16 rounded-full flex items-center justify-center ${!midiData ? 'bg-gray-700 cursor-not-allowed' : isPlayingState ? 'bg-red-500 hover:bg-red-600' : 'bg-green-500 hover:bg-green-600'} transition-all`}
          >
            {isPlayingState ? (
              <span className="text-white text-2xl"></span>
            ) : (
              <span className="text-white text-2xl"></span>
            )}
          </button>
          <button
            onClick={handleStop}
            disabled={!midiData || !isPlayingState}
            className="w-16 h-16 rounded-full bg-gray-600 hover:bg-gray-700 flex items-center justify-center transition-all disabled:opacity-50 disabled:cursor-not-allowed"
          >
            <span className="text-white text-2xl"></span>
          </button>
        </div>

        {/*  */}
        <div className="mt-6">
          <div className="flex justify-between text-sm text-gray-400 mb-2">
            <span>{formatTime(currentTime)}</span>
            <span>{formatTime(duration)}</span>
          </div>
          <input
            type="range"
            min="0"
            max={duration || 100}
            value={currentTime}
            onChange={handleSeek}
            className="w-full h-2 bg-gray-700 rounded-lg appearance-none cursor-pointer [&::-webkit-slider-thumb]:appearance-none [&::-webkit-slider-thumb]:h-4 [&::-webkit-slider-thumb]:w-4 [&::-webkit-slider-thumb]:rounded-full [&::-webkit-slider-thumb]:bg-blue-500"
          />
        </div>

        {/*  */}
        <div className="mt-6 p-4 bg-gray-800/50 rounded-lg">
          <p className="text-sm text-gray-300">
            {midiData ? (
              <> MIDI{isPlayingState ? '' : ''}</>
            ) : (
              <> MIDI...</>
            )}
          </p>
          {music.emotion && (
            <p className="text-sm text-gray-400 mt-2">
              : <span className="text-blue-400">{music.emotion}</span>
              {music.complexity && (
                <span className="ml-4">
                  : <span className="text-blue-400">{music.complexity}</span>
                </span>
              )}
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

//  -> :
const formatTime = (seconds: number) => {
  const mins = Math.floor(seconds / 60);
  const secs = Math.floor(seconds % 60);
  return `${mins}:${secs < 10 ? '0' : ''}${secs}`;
};

export default MusicPlayer;