import React, { useState } from 'react';
import './App.css';
import MusicForm from './components/MusicForm';
import MusicPlayer from './components/MusicPlayer';
import { MusicRequest, GeneratedMusic } from './types';
// 
import { generateMidiByParams } from './utils/midiGenerator';
// 
import { playMidiReal } from './utils/midiPlayer';

function App() {
  const [generatedMusic, setGeneratedMusic] = useState<GeneratedMusic | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [history, setHistory] = useState<GeneratedMusic[]>([]);
  // MIDI
  const [currentMidiData, setCurrentMidiData] = useState<any>(null);

  const handleGenerate = async (request: MusicRequest) => {
    setIsLoading(true);

    try {
      // 1. 
      const midiData = await generateMidiByParams({
        style: request.style,
        emotion: request.emotion,
        complexity: request.complexity,
      });

      // 2. MIDI
      setCurrentMidiData(midiData);

      // +++ MIDI +++
      if (typeof window !== 'undefined' && (window as any).debugMidi) {
        (window as any).debugMidi(midiData).then((hash: string) => {
          console.log(`${request.style}-${request.emotion}-${request.complexity}`);
          console.log(`${hash}`);
        });
      }

      // +++ 3.  +++
      playMidiReal(midiData, request.style).catch(console.error);

      // 4.  GeneratedMusic 
      const newMusic: GeneratedMusic = {
        id: Date.now(), // ID
        style: request.style,
        description: request.description,
        timestamp: new Date().toISOString(),
        status: 'generated',
        // 
        emotion: request.emotion,
        complexity: request.complexity,
      };

      setGeneratedMusic(newMusic);
      setHistory((prev) => [newMusic, ...prev.slice(0, 2)]);
    } catch (error) {
      console.error(':', error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
      <header className="container mx-auto px-4 py-8">
        <h1 className="text-4xl font-bold text-center mb-2">MelodyCraft</h1>
        <p className="text-center text-gray-400">AI - </p>
      </header>

      <main className="container mx-auto px-4 max-w-6xl">
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/*  */}
          <div className="lg:col-span-2 space-y-8">
            <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-6 shadow-xl border border-gray-700">
              <h2 className="text-2xl font-semibold mb-6"></h2>
              <MusicForm onSubmit={handleGenerate} isLoading={isLoading} />
            </div>

            {/*  */}
            {history.length > 0 && (
              <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-6 shadow-xl border border-gray-700">
                <h2 className="text-xl font-semibold mb-4"></h2>
                <div className="space-y-4">
                  {history.map((item) => (
                    <div key={item.id} className="bg-gray-700/50 rounded-lg p-4 hover:bg-gray-700/70 transition">
                      <div className="flex justify-between items-start">
                        <div>
                          <span className="font-medium text-lg">{item.style}</span>
                          <p className="text-gray-300 mt-1">{item.description}</p>
                          {item.emotion && (
                            <p className="text-xs text-blue-400 mt-1">: {item.emotion} | : {item.complexity}</p>
                          )}
                        </div>
                        <span className="text-sm text-gray-400">
                          {new Date(item.timestamp).toLocaleTimeString()}
                        </span>
                      </div>
                      <div className="mt-2 flex gap-2">
                        <span className="px-2 py-1 bg-green-600/20 text-green-400 rounded text-xs"></span>
                        <span className="px-2 py-1 bg-blue-600/20 text-blue-400 rounded text-xs">ID: {item.id}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/*  */}
            <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-6 shadow-xl border border-gray-700">
              <h3 className="text-lg font-semibold mb-3"></h3>
              <div className="text-sm text-gray-300 space-y-2">
                <p> <span className="text-blue-400"></span>: React + TypeScript + Tailwind CSS</p>
                <p> <span className="text-blue-400"></span>: Tonal.js + MidiWriter.js</p>
                <p> <span className="text-blue-400"></span>: Tone.js + @tonejs/midi</p>
                <p> <span className="text-blue-400"></span>: GitHub Pages</p>
                <p> <span className="text-green-400"></span>: </p>
              </div>
            </div>
          </div>

          {/*  */}
          <div className="lg:col-span-1">
            <div className="sticky top-8 space-y-8">
              <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-6 shadow-xl border border-gray-700">
                <h2 className="text-xl font-semibold mb-4"></h2>
                {/*  midiData  */}
                <MusicPlayer music={generatedMusic} midiData={currentMidiData} />
              </div>

              {/*  */}
              <div className="bg-gray-800/50 backdrop-blur-sm rounded-2xl p-6 shadow-xl border border-gray-700">
                <h3 className="text-lg font-semibold mb-4"></h3>
                <div className="space-y-3">
                  <div>
                    <p className="text-gray-400 text-sm"></p>
                    <div className="flex flex-wrap gap-2 mt-1">
                      {['React', 'TypeScript', 'Tailwind', 'Tonal.js', 'MidiWriter', 'Tone.js', 'GitHub Pages'].map((tech) => (
                        <span key={tech} className="px-3 py-1 bg-blue-600/30 rounded-full text-sm">
                          {tech}
                        </span>
                      ))}
                    </div>
                  </div>
                  <div>
                    <p className="text-gray-400 text-sm"></p>
                    <ul className="text-xs text-gray-300 mt-1 space-y-1">
                      <li> AI</li>
                      <li> 5+4</li>
                      <li> MIDI</li>
                      <li> </li>
                    </ul>
                  </div>
                  <div>
                    <p className="text-gray-400 text-sm">GitHub</p>
                    <p className="text-blue-400 hover:text-blue-300 cursor-pointer">github.com/fanfannhhh/melody-craft</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>

      <footer className="container mx-auto px-4 py-8 mt-12 text-center text-gray-500 text-sm border-t border-gray-800">
        <p>MelodyCraft  2025 - AI | </p>
        <p className="mt-2"></p>
      </footer>
    </div>
  );
}

export default App;