// src/utils/midiPlayer.ts - 
import { Midi } from '@tonejs/midi';
import * as Tone from 'tone';

// 
let activePlayer: any | null = null;
let audioContext: AudioContext | null = null;
let activeSynth: any | null = null;
let activeEffects: any[] = [];

// 
type PlayStateCallback = (isPlaying: boolean) => void;
let playStateCallbacks: PlayStateCallback[] = [];

const notifyPlayStateChange = (isPlaying: boolean) => {
  playStateCallbacks.forEach(callback => callback(isPlaying));
};

export const onPlayStateChange = (callback: PlayStateCallback) => {
  playStateCallbacks.push(callback);
  return () => {
    playStateCallbacks = playStateCallbacks.filter(cb => cb !== callback);
  };
};

/**
 * 
 */
const getSynthConfigByStyle = (style: string) => {
  const styleLower = style.toLowerCase();
  
  //  - 
  const configs: Record<string, any> = {
    // Jazz: / - 
    jazz: {
      oscillator: { 
        type: 'sine', // 
        partials: [1, 0.2, 0.05] // 
      },
      envelope: {
        attack: 0.1, // 
        decay: 0.3,
        sustain: 0.4,
        release: 0.6,
      },
      volume: -12, // 
      filter: {
        type: 'lowpass',
        frequency: 1800, // 
        rolloff: -12
      }
    },
    
    // Classical: / - 
    classical: {
      oscillator: { 
        type: 'triangle',
        partials: [1, 0.15, 0.05]
      },
      envelope: {
        attack: 0.05,
        decay: 0.2,
        sustain: 0.3,
        release: 0.5,
      },
      volume: -10,
      filter: {
        type: 'lowpass',
        frequency: 2500,
        rolloff: -12
      }
    },
    
    // Electronic:  - 
    electronic: {
      oscillator: { 
        type: 'sawtooth', // 
        partials: [1, 0.5, 0.2]
      },
      envelope: {
        attack: 0.02,
        decay: 0.15,
        sustain: 0.3,
        release: 0.3,
      },
      volume: -8,
      filter: {
        type: 'lowpass',
        frequency: 2000, // 
        Q: 1
      }
    },
    
    // Pop:  - 
    pop: {
      oscillator: { 
        type: 'sine',
        partials: [1, 0.3, 0.1]
      },
      envelope: {
        attack: 0.05,
        decay: 0.2,
        sustain: 0.35,
        release: 0.4,
      },
      volume: -10,
      filter: {
        type: 'lowpass',
        frequency: 2200,
        rolloff: -12
      }
    },
    
    // Rock: / - 
    rock: {
      oscillator: { 
        type: 'triangle',
        partials: [1, 0.4, 0.1]
      },
      envelope: {
        attack: 0.02,
        decay: 0.25,
        sustain: 0.35,
        release: 0.2,
      },
      volume: -6,
      filter: {
        type: 'lowpass',
        frequency: 2000,
        Q: 1
      }
    }
  };
  
  return configs[styleLower] || configs.classical;
};

/**
 * Base64 MIDI
 */
const parseMIDIFromBase64 = async (base64Data: string): Promise<Array<{note: number, time: number, duration: number}>> => {
  console.log('[MIDI] MIDI:', base64Data.length);
  
  try {
    // 1. Base64
    const binaryString = atob(base64Data);
    const bytes = new Uint8Array(binaryString.length);
    for (let i = 0; i < binaryString.length; i++) {
      bytes[i] = binaryString.charCodeAt(i);
    }
    
    // 2. @tonejs/midi
    const midi = new Midi(bytes);
    const notes: Array<{note: number, time: number, duration: number}> = [];
    
    // 3. 
    midi.tracks.forEach((track, trackIndex) => {
      track.notes.forEach(midiNote => {
        notes.push({
          note: midiNote.midi,      // MIDI0-127
          time: midiNote.time,       // 
          duration: midiNote.duration // 
        });
      });
      console.log(`[MIDI] ${trackIndex}: ${track.notes.length}`);
    });
    
    console.log(`[MIDI]  ${notes.length} `);
    
    if (notes.length === 0) {
      console.warn('[MIDI] ');
      return generateFallbackNotes();
    }
    
    return notes;
  } catch (error) {
    console.error('[MIDI] MIDI:', error);
    console.log('[MIDI] ');
    return generateFallbackNotes();
  }
};

/**
 * 
 */
const generateFallbackNotes = (): Array<{note: number, time: number, duration: number}> => {
  const notes: Array<{note: number, time: number, duration: number}> = [];
  const noteCount = 16;
  
  for (let i = 0; i < noteCount; i++) {
    const note = 60 + (i % 8);
    notes.push({
      note,
      time: i * 0.5,
      duration: 0.4
    });
  }
  
  return notes;
};

/**
 * 
 */
const cleanupEffects = () => {
  activeEffects.forEach(effect => {
    if (effect && effect.dispose) {
      effect.dispose();
    }
  });
  activeEffects = [];
};

/**
 * Tone.js
 */
const playNotesWithTone = async (
  notes: Array<{note: number, time: number, duration: number}>, 
  style: string = 'classical'
) => {
  await Tone.start();
  
  // 
  cleanupEffects();
  
  // 
  const synthConfig = getSynthConfigByStyle(style);
  
  // 
  const synth = new Tone.PolySynth(Tone.Synth, {
    oscillator: synthConfig.oscillator,
    envelope: synthConfig.envelope,
  });
  
  // 
  const filter = new Tone.Filter({
    type: 'lowpass',
    frequency: synthConfig.filter?.frequency || 3000,
    rolloff: synthConfig.filter?.rolloff || -12,
    Q: synthConfig.filter?.Q || 1
  }).toDestination();
  
  synth.connect(filter);
  activeEffects.push(filter);
  
  // 
  synth.volume.value = synthConfig.volume || -10;
  
  // 
  if (style.toLowerCase() === 'jazz') {
    const reverb = new Tone.Reverb({
      decay: 2.0, // 
      wet: 0.2    // 
    }).toDestination();
    filter.connect(reverb);
    activeEffects.push(reverb);
  } else if (style.toLowerCase() === 'rock') {
    const distortion = new Tone.Distortion(0.2).toDestination(); // 
    filter.connect(distortion);
    activeEffects.push(distortion);
  } else if (style.toLowerCase() === 'electronic') {
    const pingPongDelay = new Tone.PingPongDelay({
      delayTime: "8n",
      feedback: 0.2, // 
      wet: 0.15      // 
    }).toDestination();
    filter.connect(pingPongDelay);
    activeEffects.push(pingPongDelay);
  }
  
  activeSynth = synth;
  
  // 
  const now = Tone.now();
  notes.forEach(note => {
    const frequency = Tone.Frequency(note.note, "midi").toFrequency();
    synth.triggerAttackRelease(frequency, note.duration, now + note.time);
  });
  
  // 
  let totalDuration = 0;
  if (notes.length > 0) {
    const lastNote = notes[notes.length - 1];
    totalDuration = lastNote.time + lastNote.duration;
  }
  
  console.log(`[MIDI] ${style}${notes.length}`);
  
  return {
    stop: () => {
      synth.releaseAll();
      console.log('[MIDI] ');
      cleanupEffects();
    },
    totalDuration
  };
};

/**
 * style
 */
export const playMidiReal = async (base64Data: string, style: string = 'classical'): Promise<void> => {
  try {
    console.log(`[MIDI] ${style}`);
    
    // 1. 
    stopMidi();
    
    // 2. MIDI
    const notes = await parseMIDIFromBase64(base64Data);
    
    if (notes.length === 0) {
      console.warn('[MIDI] ');
      return;
    }
    
    console.log(`[MIDI]  ${notes.length} ${style}`);
    
    // 3. Tone.js
    const player = await playNotesWithTone(notes, style);
    
    // 4. 
    activePlayer = {
      stop: player.stop,
      totalDuration: player.totalDuration
    };
    
    // 5. MIDI
    setTimeout(() => {
      if (activePlayer) {
        console.log(`[MIDI] ${style}`);
        stopMidi();
      }
    }, player.totalDuration * 1000 + 1000); // 1
    
    // 6. 
    notifyPlayStateChange(true);
    
    console.log(`[MIDI] : ${player.totalDuration.toFixed(1)}`);
  } catch (error) {
    console.error('[MIDI] :', error);
    stopMidi();
  }
};

/**
 * 
 */
export const stopMidi = (): void => {
  if (activePlayer) {
    activePlayer.stop();
    activePlayer = null;
  }
  
  if (activeSynth) {
    activeSynth.releaseAll();
    activeSynth.dispose();
    activeSynth = null;
  }
  
  cleanupEffects();
  
  console.log('[MIDI] ');
  notifyPlayStateChange(false);
};

/**
 * 
 */
export const isPlaying = (): boolean => {
  return activePlayer !== null;
};

/**
 * 
 */
export const initAudio = async (): Promise<AudioContext> => {
  if (audioContext) return audioContext as AudioContext;
  
  await Tone.start();
  // @ts-ignore - Tone.js 
  audioContext = Tone.context.rawContext;
  console.log('[MIDI] ');
  return audioContext as AudioContext;
};