import React, { useState } from 'react';
import { MusicFormProps, MusicStyle } from '../types';

const MUSIC_STYLES: MusicStyle[] = ['Jazz', 'Classical', 'Electronic', 'Pop', 'Rock'];
type MusicEmotion = 'happy' | 'sad' | 'peaceful' | 'energetic';

//  - 
const MUSIC_TAGS = [
  {
    id: 'melancholy_jazz',
    label: '',
    icon: '',
    style: 'Jazz' as MusicStyle,
    emotion: 'sad' as MusicEmotion,
    complexity: 2,
    description: ''
  },
  {
    id: 'happy_pop',
    label: '',
    icon: '',
    style: 'Pop' as MusicStyle,
    emotion: 'happy' as MusicEmotion,
    complexity: 3,
    description: ''
  },
  {
    id: 'epic_rock',
    label: '',
    icon: '',
    style: 'Rock' as MusicStyle,
    emotion: 'energetic' as MusicEmotion,
    complexity: 4,
    description: ''
  },
  {
    id: 'calm_classical',
    label: '',
    icon: '',
    style: 'Classical' as MusicStyle,
    emotion: 'peaceful' as MusicEmotion,
    complexity: 3,
    description: ''
  },
  {
    id: 'electronic_dance',
    label: '',
    icon: '',
    style: 'Electronic' as MusicStyle,
    emotion: 'energetic' as MusicEmotion,
    complexity: 5,
    description: ''
  },
  {
    id: 'jazzy_fusion',
    label: '',
    icon: '',
    style: 'Jazz' as MusicStyle,
    emotion: 'happy' as MusicEmotion,
    complexity: 4,
    description: ''
  }
];

const MusicForm: React.FC<MusicFormProps> = ({ onSubmit, isLoading }) => {
  const [selectedTagId, setSelectedTagId] = useState<string>('happy_pop');
  const [style, setStyle] = useState<MusicStyle>('Pop');
  const [emotion, setEmotion] = useState<MusicEmotion>('happy');
  const [complexity, setComplexity] = useState<number>(3);

  // 
  const handleTagSelect = (tagId: string) => {
    setSelectedTagId(tagId);
    const selectedTag = MUSIC_TAGS.find(tag => tag.id === tagId);
    if (selectedTag) {
      setStyle(selectedTag.style);
      setEmotion(selectedTag.emotion);
      setComplexity(selectedTag.complexity);
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    const selectedTag = MUSIC_TAGS.find(tag => tag.id === selectedTagId);
    
    await onSubmit({
      style,
      description: selectedTag ? selectedTag.description : 'AI',
      emotion,
      complexity
    });
  };

  // 
  const currentTag = MUSIC_TAGS.find(tag => tag.id === selectedTagId);

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/*  */}
      <div>
        <label className="block text-sm font-medium text-gray-300 mb-3"></label>
        <div className="grid grid-cols-2 gap-3">
          {MUSIC_TAGS.map((tag) => (
            <button
              key={tag.id}
              type="button"
              onClick={() => handleTagSelect(tag.id)}
              className={`p-4 rounded-xl transition-all flex flex-col items-center justify-center ${
                selectedTagId === tag.id
                  ? 'bg-gradient-to-r from-blue-600/30 to-purple-600/30 border-2 border-blue-500'
                  : 'bg-gray-800/50 border border-gray-700 hover:bg-gray-700/50'
              }`}
            >
              <span className="text-2xl mb-2">{tag.icon}</span>
              <span className="font-medium text-white">{tag.label}</span>
              <span className="text-xs text-gray-400 mt-1">{tag.description}</span>
            </button>
          ))}
        </div>
      </div>

      {/*  */}
      {currentTag && (
        <div className="p-4 bg-gray-800/30 rounded-lg border border-gray-700">
          <div className="flex items-center gap-3 mb-3">
            <span className="text-2xl">{currentTag.icon}</span>
            <div>
              <h4 className="font-medium text-white">{currentTag.label}</h4>
              <p className="text-sm text-gray-400">{currentTag.description}</p>
            </div>
          </div>
          <div className="grid grid-cols-3 gap-4 text-sm">
            <div>
              <span className="text-gray-400">:</span>
              <span className="ml-2 text-blue-400">{currentTag.style}</span>
            </div>
            <div>
              <span className="text-gray-400">:</span>
              <span className="ml-2 text-green-400">{currentTag.emotion}</span>
            </div>
            <div>
              <span className="text-gray-400">:</span>
              <span className="ml-2 text-yellow-400">{currentTag.complexity}/5</span>
            </div>
          </div>
        </div>
      )}

      {/*  */}
      <div className="p-4 bg-gray-900/30 rounded-lg">
        <h4 className="font-medium text-gray-300 mb-4"></h4>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2"></label>
            <select
              value={emotion}
              onChange={(e) => setEmotion(e.target.value as MusicEmotion)}
              className="w-full p-3 bg-gray-900 border border-gray-700 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value="happy">  (Happy)</option>
              <option value="sad">  (Sad)</option>
              <option value="peaceful">  (Peaceful)</option>
              <option value="energetic">  (Energetic)</option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2">
              : {complexity}
            </label>
            <input
              type="range"
              min="1"
              max="5"
              step="1"
              value={complexity}
              onChange={(e) => setComplexity(parseInt(e.target.value))}
              className="w-full h-2 bg-gray-700 rounded-lg appearance-none cursor-pointer [&::-webkit-slider-thumb]:appearance-none [&::-webkit-slider-thumb]:h-4 [&::-webkit-slider-thumb]:w-4 [&::-webkit-slider-thumb]:rounded-full [&::-webkit-slider-thumb]:bg-blue-500"
            />
            <div className="flex justify-between text-xs text-gray-400 mt-1">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-300 mb-2"></label>
            <div className="flex flex-wrap gap-2">
              {MUSIC_STYLES.map((musicStyle) => (
                <button
                  key={musicStyle}
                  type="button"
                  onClick={() => setStyle(musicStyle)}
                  className={`px-4 py-2 rounded-lg transition-all ${
                    style === musicStyle
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                  }`}
                >
                  {musicStyle}
                </button>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <button
        type="submit"
        disabled={isLoading}
        className={`w-full py-4 px-4 rounded-lg font-medium transition-all ${
          isLoading
            ? 'bg-gray-700 cursor-not-allowed text-gray-400'
            : 'bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white'
        }`}
      >
        {isLoading ? (
          <span className="flex items-center justify-center">
            <svg
              className="animate-spin h-5 w-5 mr-2 text-white"
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
            >
              <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" />
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              ></path>
            </svg>
            ...
          </span>
        ) : (
          `${currentTag?.label || ''}`
        )}
      </button>

      <div className="p-4 bg-gray-900/50 rounded-lg">
        <h4 className="font-medium text-gray-300 mb-2"></h4>
        <ul className="text-sm text-gray-400 space-y-1">
          <li>1. </li>
          <li>2. </li>
          <li>3. AI</li>
          <li>4. </li>
        </ul>
      </div>
    </form>
  );
};

export default MusicForm;