import { Scale, Chord } from 'tonal';
import MidiWriter from 'midi-writer-js';
import { MusicStyle } from '../types';

type InternalStyle = Lowercase<MusicStyle> | 'electronic';

interface StyleConfig {
  scale: string;
  chords: string[];
  bpm: number;
  rhythm: 'smooth' | 'bouncy' | 'driving' | 'flowing';
  intensity: number; // 1-5
}

// 
const STYLE_CONFIG: Record<InternalStyle, StyleConfig> = {
  jazz: {
    scale: 'C major',
    chords: ['Cmaj7', 'Am7', 'Dm7', 'G7', 'Fmaj7', 'Em7', 'A7', 'D7'],
    bpm: 100,
    rhythm: 'smooth',
    intensity: 3
  },
  classical: {
    scale: 'G major',
    chords: ['G', 'Em', 'C', 'D', 'Am', 'Bm', 'F#dim', 'A'],
    bpm: 85,
    rhythm: 'flowing',
    intensity: 2
  },
  electronic: {
    scale: 'A minor',
    chords: ['Am', 'F', 'C', 'G', 'Dm', 'Em', 'E', 'D'],
    bpm: 128,
    rhythm: 'driving',
    intensity: 4
  },
  pop: {
    scale: 'C major',
    chords: ['C', 'G', 'Am', 'F', 'Em', 'Dm', 'D', 'A'],
    bpm: 112,
    rhythm: 'bouncy',
    intensity: 3
  },
  rock: {
    scale: 'E minor',
    chords: ['Em', 'C', 'G', 'D', 'Am', 'F', 'B', 'A'],
    bpm: 126,
    rhythm: 'driving',
    intensity: 5
  },
};

const toInternalStyle = (style: string): InternalStyle => {
  const normalized = style.toLowerCase() as InternalStyle;
  if (normalized in STYLE_CONFIG) {
    return normalized;
  }
  return 'pop';
};

// 
const EMOTION_TO_MUSIC = {
  happy: {
    chordType: 'major',
    tempoChange: 10,
    noteRange: 'high',
    articulation: 'staccato'
  },
  sad: {
    chordType: 'minor',
    tempoChange: -15,
    noteRange: 'mid-low',
    articulation: 'legato'
  },
  peaceful: {
    chordType: 'major',
    tempoChange: -20,
    noteRange: 'mid',
    articulation: 'legato'
  },
  energetic: {
    chordType: 'major',
    tempoChange: 20,
    noteRange: 'wide',
    articulation: 'staccato'
  }
} as const;

// 
const COMPLEXITY_TO_MUSIC = {
  1: { chordsPerBar: 1, notesPerBar: 2, variation: 0.1 },
  2: { chordsPerBar: 1, notesPerBar: 3, variation: 0.2 },
  3: { chordsPerBar: 2, notesPerBar: 4, variation: 0.3 },
  4: { chordsPerBar: 2, notesPerBar: 5, variation: 0.4 },
  5: { chordsPerBar: 3, notesPerBar: 6, variation: 0.5 }
} as const;

/**
 * 
 */
const adjustChordsByEmotion = (chords: string[], emotion: string): string[] => {
  const emotionConfig = EMOTION_TO_MUSIC[emotion as keyof typeof EMOTION_TO_MUSIC];
  if (!emotionConfig) return chords;

  return chords.map(chord => {
    if (emotionConfig.chordType === 'minor' && !chord.includes('m') && !chord.includes('dim')) {
      // 
      const root = chord.replace(/[^A-G]/g, '');
      return root + 'm';
    }
    return chord;
  });
};

/**
 * 
 */
const getNoteRangeByEmotion = (emotion: string, baseNote: string = 'C4'): [string, string] => {
  const ranges: Record<string, [string, string]> = {
    happy: ['G4', 'C6'],      // 
    sad: ['C3', 'G4'],        // 
    peaceful: ['E4', 'B5'],    // 
    energetic: ['G3', 'D6']    // 
  };
  
  return ranges[emotion] || ['C4', 'G5'];
};

/**
 *  - 
 */
const createMelodyTrack = (
  scaleNotes: string[],
  totalBars: number,
  emotion: string,
  complexity: number,
  style: string
) => {
  const track = new MidiWriter.Track();
  track.addEvent(new MidiWriter.ProgramChangeEvent({ instrument: 1 }));

  const complexityConfig = COMPLEXITY_TO_MUSIC[complexity as keyof typeof COMPLEXITY_TO_MUSIC];
  const notesPerBar = complexityConfig.notesPerBar;
  const noteRange = getNoteRangeByEmotion(emotion);
  
  // 
  const allNotes = scaleNotes.map(note => {
    const noteName = note.replace(/\d/g, '');
    const octave = parseInt(note.replace(/[^\d]/g, '')) || 4;
    return `${noteName}${octave}`;
  });

  // 
  const rhythmPatterns: Record<string, string[]> = {
    jazz: ['8', '8', '4', '8d'], // 
    classical: ['4', '4', '4', '4'], // 
    electronic: ['16', '16', '8', '16'], // 
    pop: ['4', '8', '8', '4'], // 
    rock: ['8', '8', '8', '8'] // 
  };

  const rhythm = rhythmPatterns[style] || ['4', '4', '4', '4'];

  for (let bar = 0; bar < totalBars; bar += 1) {
    for (let i = 0; i < notesPerBar; i += 1) {
      // 
      let pitchPool = allNotes;
      
      if (emotion === 'sad') {
        // 
        pitchPool = allNotes.filter(note => {
          const octave = parseInt(note.replace(/[^\d]/g, '')) || 4;
          return octave <= 4;
        });
      } else if (emotion === 'happy') {
        // 
        pitchPool = allNotes.filter(note => {
          const octave = parseInt(note.replace(/[^\d]/g, '')) || 4;
          return octave >= 4;
        });
      }

      // 
      const shouldVary = Math.random() < complexityConfig.variation;
      const pitch = shouldVary && i > 0 ? 
        transposeNote(pitchPool[Math.floor(Math.random() * pitchPool.length)], 2) : 
        pitchPool[Math.floor(Math.random() * pitchPool.length)];

      const duration = rhythm[i % rhythm.length];
      const velocity = emotion === 'energetic' ? 90 : emotion === 'sad' ? 60 : 80;

      track.addEvent(
        new MidiWriter.NoteEvent({
          pitch: [pitch],
          duration: duration as any,
          velocity,
        }),
      );
    }
  }

  return track;
};

/**
 * 
 */
const transposeNote = (note: string, steps: number): string => {
  const noteName = note.replace(/\d/g, '');
  const octave = parseInt(note.replace(/[^\d]/g, '')) || 4;
  const notes = ['C', 'C#', 'D', 'D#', 'E', 'F', 'F#', 'G', 'G#', 'A', 'A#', 'B'];
  let index = notes.indexOf(noteName);
  
  if (index === -1) {
    // 
    const flatNotes = ['C', 'Db', 'D', 'Eb', 'E', 'F', 'Gb', 'G', 'Ab', 'A', 'Bb', 'B'];
    index = flatNotes.indexOf(noteName);
    if (index === -1) return note;
    notes[index] = flatNotes[index];
  }

  const newIndex = (index + steps) % 12;
  const newOctave = octave + Math.floor((index + steps) / 12);
  return `${notes[newIndex]}${newOctave}`;
};

/**
 *  - 
 */
const createChordTrack = (
  chords: string[],
  totalBars: number,
  emotion: string,
  complexity: number,
  style: string
) => {
  const track = new MidiWriter.Track();
  track.addEvent(new MidiWriter.ProgramChangeEvent({ instrument: 0 }));

  const complexityConfig = COMPLEXITY_TO_MUSIC[complexity as keyof typeof COMPLEXITY_TO_MUSIC];
  const chordsPerBar = complexityConfig.chordsPerBar;

  for (let bar = 0; bar < totalBars; bar += 1) {
    const chordsThisBar = [];
    
    // 
    for (let i = 0; i < chordsPerBar; i++) {
      const chordIndex = (bar * chordsPerBar + i) % chords.length;
      chordsThisBar.push(chords[chordIndex]);
    }

    // 
    chordsThisBar.forEach((chordSymbol, index) => {
      const chordNotes = Chord.get(chordSymbol).notes;
      if (!chordNotes || chordNotes.length === 0) {
        // 
        track.addEvent(
          new MidiWriter.NoteEvent({
            pitch: ['C3', 'E3', 'G3'],
            duration: Math.floor(4 / chordsPerBar).toString() as any,
            velocity: 60,
          }),
        );
        return;
      }

      // 
      const bass = `${chordNotes[0]}2`;
      const chordVoicing = chordNotes.slice(0, 3).map((n) => `${n}3`);
      
      // 
      if (complexity >= 4 && chordNotes.length > 3) {
        chordVoicing.push(`${chordNotes[3]}4`);
      }

      const duration = Math.floor(4 / chordsPerBar).toString() as any;
      const velocity = emotion === 'energetic' ? 70 : emotion === 'peaceful' ? 50 : 60;

      track.addEvent(
        new MidiWriter.NoteEvent({
          pitch: [bass, ...chordVoicing],
          duration,
          velocity,
        }),
      );
    });
  }

  return track;
};

// 
export interface GenerationParams {
  style: string;
  emotion: 'happy' | 'sad' | 'peaceful' | 'energetic';
  complexity: number; // 1-5
}

/**
 *  - 
 */
export const generateMidiByParams = async (
  params: GenerationParams
): Promise<string> => {
  const { style, emotion, complexity } = params;

  // 
  const internalStyle = toInternalStyle(style);
  const baseConfig = STYLE_CONFIG[internalStyle];

  // BPM
  const emotionConfig = EMOTION_TO_MUSIC[emotion as keyof typeof EMOTION_TO_MUSIC];
  const adjustedBpm = Math.max(60, Math.min(160, baseConfig.bpm + emotionConfig.tempoChange));

  // 
  const adjustedChords = adjustChordsByEmotion(baseConfig.chords, emotion);

  // 
  const baseDuration = 12 + complexity * 3; // 12-27
  const bars = Math.max(4, Math.floor((adjustedBpm * (baseDuration / 60)) / 4));

  // 
  const scaleNotes = Scale.get(baseConfig.scale).notes.map((n) => `${n}4`);

  console.log(`: ${style}-${emotion}-${complexity}`);
  console.log(`BPM: ${adjustedBpm}, : ${bars}, : ${adjustedChords.slice(0, 4).join(', ')}...`);

  const file = new MidiWriter.Writer([]);

  // 
  const chordTrack = createChordTrack(adjustedChords, bars, emotion, complexity, internalStyle);
  
  // 
  const melodyTrack = createMelodyTrack(scaleNotes, bars, emotion, complexity, internalStyle);

  file.tracks.push(chordTrack);
  file.tracks.push(melodyTrack);

  const dataUri = file.dataUri();
  const base64 = dataUri.split(',')[1] ?? '';
  
  return base64;
};

// 
export const generateMIDI = async (
  style: string,
  description: string,
  durationSeconds: number = 16,
): Promise<string> => {
  // 
  return generateMidiByParams({
    style,
    emotion: 'happy',
    complexity: 3
  });
};

export const midiBase64ToDataUrl = (base64: string) =>
  `data:audio/midi;base64,${base64}`;